.contact {
    height: 100vh;
    width: 100%;
    position: relative;

    @media screen and (max-width: 500px) {
        height: auto;
        margin-top: 80px;
        overflow-y: hidden;
    }

    .contact-bg {
        width: 20px;
        height: 100%;
        background: #4d84e1;
        position: absolute;

        @media screen and (max-width: 500px) {
            display: none;
        }
    }

    .contact-wrapper {
        padding: 100px;
        display: flex;
        height: 882px;
        position: absolute;
        bottom: 0px;
        overflow-y: hidden;
        width: 100%;

        @media screen and (max-width: 500px) {
            flex-direction: column;
            position: relative;
            height: 100%;
            padding: 0px;
            top: 10px;
            left: 20px;
        }

        .contact-left {
            flex: 1;

            @media screen and (max-width: 1100px) {
                flex: 2;
            }

            .contact-title {
                font-size: 48px;
                width: 80%;
                margin-bottom: 20px;

                @media screen and (max-width: 500px) {
                    font-size: 30px;
                    margin-bottom: 20px;
                }
            }

            .contact-info {
                @media screen and (max-width: 500px) {
                    margin: 50px 0px;
                }
                .contact-info-item {
                    display: flex;
                    align-items: center;
                    margin: 50px 0px;
                    font-weight: 300;
                    width: 70%;

                    @media screen and (max-width: 500px) {
                        margin: 20px 0px;
                        width: 100%;
                    }

                    .contact-icon {
                        width: 30px;
                        height: 30px;
                        margin-right: 20px;

                        @media screen and (max-width: 500px) {
                            width: 20px;
                            height: 20px;
                        }
                    }
                }
            }

            .call-me-memoji {
                position: absolute;

                @media screen and (max-width: 500px) {
                    display: none;
                }

                @media screen and (max-width: 1400px) {
                    display: none;
                }
            }
        }

        .contact-right {
            flex: 1;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            margin-left: 200px;

            @media screen and (max-width: 500px) {
                margin-left: 0px;
                align-items: baseline;
                justify-content: baseline;
            }

            @media screen and (max-width: 1100px) {
                flex: 2;
            }

            .contact-container {
                display: flex;
                flex-direction: column;
                align-items: center;

                .contact-description {
                    font-weight: 300;
                    font-size: 18px;
                    word-wrap: normal;
                    max-width: 500px;
                    margin-left: 10px;
                    @media screen and (max-width: 700px) {
                        display: none;
                    }
                }

                form {
                    margin-bottom: 30px;
                    position: relative;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    height: 100%;
                    width: 600px;

                    @media screen and (max-width: 700px) {
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        width: 100%;
                        margin-left: 20px;
                    }

                    @media screen and (max-width: 1400px) {
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        width: 100%;
                    }

                    input {
                        width: 80%;
                        height: 50px;
                        border: none;
                        border-bottom: 1px solid #000;
                        margin-right: 10px;
                        margin-bottom: 10px;
                        padding-left: 10px;

                        @media screen and (max-width: 700px) {
                            width: 100%;
                        }
                        @media screen and (max-width: 1400px) {
                            width: 80%;
                        }
                    }

                    textarea {
                        width: 80%;
                        margin: 10px 0px;
                        font-size: 14px;
                        padding-left: 10px;
                        resize: none;

                        @media screen and (max-width: 500px) {
                            width: 80%;
                        }
                    }

                    button {
                        border: none;
                        width: 80%;
                        margin-top: 10px;
                        padding: 15px;
                        background-color: #4d84e1;
                        color: white;
                        font-weight: 500;
                        cursor: pointer;

                        &:hover {
                            background-color: #416db9;
                        }
                    }
                }
            }
        }
    }
}
